<template>
    <transition name="slide">
        <div class="categories" v-if="categories">
            <div class="categories-container">
                <h6 class="all-categories-btn btn btn-sm btn-outline-cat">
                    All Categories
                </h6>
                <ul
                    class="list-unstyled d-flex flex-wrap justify-content-center"
                >
                    <li
                        class="categories-cat"
                        v-for="cat in allCats"
                        :key="cat.id"
                    >
                        <router-link to="" class="btn btn-sm btn-outline-cat">
                            {{ cat.name }}
                        </router-link>
                    </li>
                </ul>
            </div>
        </div>
    </transition>
</template>

<script>
import axios from "axios";
import { mapGetters } from "vuex";
export default {
    name: "AllCats",
    computed: mapGetters(["categories"]),
    data() {
        return {
            allCats: [],
        };
    },
    async created() {
        const response = await axios.get("/json/categories.json");
        this.allCats = response.data;
    },
};
</script>

<style lang="scss" scoped>
@import "@/variables";

.categories {
    height: calc(100vh - 100px);
    user-select: none;
    .categories-container {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        height: 70%;
        width: 60%;
        margin: 0 auto;
    }
    .all-categories-btn {
        width: 50%;
        margin: 15px auto;
        padding: 5px 8px;
    }
    .categories-cat {
        margin: 4px 15px;
        transition: all 0.2s;
    }
    .categories-cat:hover {
        margin: 12px 10px 5px 7px;
        transition: all 0.2s;
    }
    .btn-outline-cat {
        color: $dark_color;
        border-color: $dark_color;
        box-shadow: 1px 1px 1px $light_color, -1px -1px 1px $light_color;
    }
    .btn-outline-cat:hover {
        background-color: $dark_color;
        border-color: $light_color;
        color: $light_color;
        transform: scale(1.3);
        transform-origin: bottom;
        box-shadow: 3px 1px 4px $light_color, -2px -2px 4px $light_color;
    }
}
/*========================================
========== Small Screen =================
========================================*/
@media (max-width: 768px) {
    .categories {
        height: calc(100vh - 100px);
        .categories-container {
            height: 70%;
            width: 90%;
            justify-content: center;
            .all-categories-btn {
                margin: 25px auto;
            }
        }
        .categories-cat {
            margin: 3px 8px;
        }
    }
}
/*========================================
========== Very Small Screen =============
========================================*/
@media (max-width: 450px) {
    .categories {
        .categories-container {
            width: 98%;
            height: 90% !important;
            .all-categories-btn {
                margin: 20px auto;
            }
        }
        .categories-cat {
            margin: 1px 2px;
        }
    }
    .search {
        .search-container {
            width: 80%;
            bottom: 5%;
        }
    }

    .books {
        top: 20%;
        left: 1rem;
        img {
            width: 50px;
        }
        .books-arrow {
            right: -25px;
        }
    }
}

/*======== Transition Animation ========*/
/*======== Transition Animation ========*/
.slide-enter {
    opacity: 0;
}
.slide-enter-active {
    opacity: 0;
    transition: 0.5s;
}
.slide-enter-to {
    opacity: 0;
}
.slide-leave {
    opacity: 0;
}
.slide-leave-active {
    opacity: 0;
}
.slide-leave-to {
    opacity: 0;
}
</style>
