const state = {
    categories: false
};
const getters = {
    categories: state => state.categories
};
const actions = {
    showCategories: ({ commit }) => commit("showCategories")
};
const mutations = {
    showCategories: state => (state.categories = !state.categories)
};

export default {
    state,
    getters,
    actions,
    mutations
};
