<template>
    <nav class="navbar" id="navbar">
        <ul class="navbar-menu list-unstyled d-flex justify-content-center">
            <li class="navbar-item active">
                <a class="navbar-link" href="index.html">Home</a>
            </li>
            <li class="navbar-item">
                <a class="navbar-link" href="">Upload</a>
            </li>
            <li class="navbar-item">
                <a class="navbar-link" href="">Downlaod</a>
            </li>
        </ul>
        <button class="navbar-button" type="button">
            <i class="fa fa-list"></i>
        </button>
    </nav>
</template>

<script>
export default {
    name: "Navbar",
    mounted() {
        const navbar = document.getElementById("navbar");
        window.addEventListener("scroll", function() {
            if (window.pageYOffset > 500) {
                const classString = navbar.className;
                if (classString.indexOf("navbar-fixed") === -1) {
                    const newClasses = classString.concat(" navbar-fixed");
                    navbar.className = newClasses;
                }
            } else {
                const newClasses = navbar.className.substring(0, 7);
                navbar.className = newClasses;
            }
        });
    }
};
</script>
<style lang="scss" scoped>
@import "@/variables";

.navbar {
    width: 100vw;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 100000;
    padding: 0;
    background: transparent;
    display: none;
    transition: background 0.4s, all 0.3s linear;
    .navbar-menu {
        width: 100%;
        margin: 0;
        .navbar-item {
            margin: 10px 15px;
            padding: 5px;
            position: relative;
            cursor: pointer;
            &:after {
                content: "";
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 0;
                background-color: $light_color;
                transition: all 0.4s ease;
                z-index: -1;
                border-radius: 2px;
                box-shadow: inset 0 0 5px $dark_color;
            }
            .navbar-link {
                color: $light_color;
                text-decoration: none;
                font-size: 1rem;
                position: relative;
                z-index: 1;
            }
            &:hover {
                &:after {
                    height: 100%;
                }
                .navbar-link {
                    font-weight: 200;
                    color: $dark_color;
                }
            }
        }
    }
    .navbar-button {
        display: none;
    }
}
.navbar-fixed {
    position: fixed;
    background: rgba(242, 109, 53, 0.5);
    box-shadow: inset 0px 0px 12px $dark_color;
    display: block;
}
</style>
