<template>
    <div
        class="flip-button"
        :style="
            categories
                ? { transform: 'rotate(90deg)' }
                : { transform: 'rotate(0)' }
        "
    >
        <span class="flip-note" v-if="!categories"></span>
        <span
            class="flip-arrow fa fa-arrow-circle-left"
            v-if="!categories"
        ></span>
        <img
            id="flip"
            src="images/all-categories.png"
            alt=""
            width="90px"
            @click="showCategories"
        />
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
    name: "FlipButton",
    computed: mapGetters(["categories"]),
    methods: mapActions(["showCategories"]),
    mounted() {
        // TypeWriter Effect
        setInterval(typeWriterEffect, 6000);
        function typeWriterEffect() {
            const flipNote = document.querySelector(".flip-note");
            const flipArrow = document.querySelector(".flip-arrow");
            if (flipNote && flipArrow) {
                const noteText = "Click here to show all Categories ...";
                let printedText = "",
                    i = 0;
                flipNote.style.display = "inline";
                flipNote.innerText = "";
                flipArrow.style.display = "block";
                var timer = setInterval(function() {
                    printedText += noteText.charAt(i);
                    flipNote.innerText = printedText;
                    if (i === noteText.length) {
                        clearInterval(timer);
                        // flipNote.style.display = "none";
                        // flipArrow.style.display = "none";
                    }
                    i++;
                }, 60);
            }
        }
    }
};
</script>

<style lang="scss" scoped>
.flip-button {
    position: absolute;
    top: 20vh;
    left: 0.1rem; //Same as Padding of the header
    transform: rotate(0deg);
    transition: all 0.7s;
    img {
        cursor: pointer;
    }
    .flip-note {
        position: absolute;
        top: -5px;
        left: 0;
        font-size: 10px;
        width: 300px;
        text-align: left;
        padding-left: 10px;
    }
    .flip-arrow {
        position: absolute;
        top: 25%;
        right: 0;
        animation: arrow infinite 6s;
        display: none;
    }
    @keyframes arrow {
        0% {
            right: -10px;
        }
        5% {
            right: 0;
        }
        10% {
            right: -10px;
        }
        15% {
            right: 0;
        }
        20% {
            right: -10px;
        }
        25% {
            right: 0;
        }
        30% {
            right: -10px;
        }
        100% {
            right: -10px;
        }
    }
    &:hover {
        filter: brightness(1.5);
    }
    &.rotate {
        transform: rotate(-90deg);
    }
}

@media (max-width: 768px) {
    .flip-button {
        left: 0.1rem;
    }
}
@media (max-width: 450px) {
    .flip-button {
        img {
            width: 80px;
        }
    }
}
</style>
